'use client';
import { Box, List, ListItem } from '@mui/material';
import { LanguageSelect } from '@src/components/language-selector/LanguageSelect';
// import { TabbarContainer } from '@src/components/tabbar/Tabbar';
import { AppShell, TabbarContainer } from '@app/ui';
import { createContext, useContext } from 'react';

import { appProxyModel } from '@app/model';
import { useProxyWatch } from '@fanfanlo';
function VisitInfo(){
  const [visitInfo] = useProxyWatch(appProxyModel, 'visitInfo', undefined)
  return <div>{JSON.stringify(visitInfo)}</div>
}
const C = createContext(new Date());
function Me() {
  const U = useContext(C);
  return (
    <AppShell>
      <TabbarContainer titleConf={{ns:"homepage/pages/me/content", key:"me.title"}} >
      <Box data-id="page-me-content">
        {/* <VisitInfo /> */}
      <LanguageSelect />
      <List>

        <ListItem key="a">
          <Box>
            {/* <div>me{U.getTime()}</div> */}
            {new Array(130).fill(0).map((_, i) => <div key={i}>{i}</div>)}
          </Box>
        </ListItem>
      </List>
      </Box>
    </TabbarContainer>
    </AppShell>
  );
}
export default Me;




